import React, { useContext } from 'react'
import { OrderContext } from '../../services/OrderProvider/OrderProvider'

const SlotSelect = () => {
  const orderContext = useContext(OrderContext);
  const selectHandler = (e) => {
    orderContext.setOrder({ ...orderContext.order, slot: { day: "Monday", time: "08:00" } })
  }
  return (
    <div>
      <h3>Select Booking Slot</h3>
      <ul>
        <li>
          <p>Monday, 08:00</p>
          <button onClick={selectHandler}>select</button>
        </li>
      </ul>
    </div>
  )
}

export default SlotSelect
