import React, { useContext } from 'react'
import { Link } from 'react-router-dom'
import { OrderContext } from '../../services/OrderProvider/OrderProvider'

const TyreSelect = () => {
  const orderContext = useContext(OrderContext);
  const selectHandler = (e) => {
    orderContext.setOrder({ ...orderContext.order, tyre: "Goodyear P215/65 R15" })
  }
  return (

    <div>
      <h3>Select Tyre</h3>
      <img src="https://www.specialceramics.in/wp-content/uploads/2019/11/Carbon-Black-2.png" alt="tyre" />
      <Link to="/fitterselect"><button onClick={selectHandler}>Select</button></Link>
    </div>
  )
}

export default TyreSelect
