import React from 'react'
import { Link } from 'react-router-dom'


const Navbar = () => {
  return (
    <nav>
      <li>
        <Link to="/">Home</Link>
      </li>
      <li>
        <Link to="/tyreselect">Select Tyre</Link>
      </li>
      <li>
        <Link to="/fitterselect">Select Fitter</Link>
      </li>
      <li>
        <Link to="/slotselect">Select Slot</Link>
      </li>
    </nav>
  )
}

export default Navbar
