import React from 'react'
// React Router
import {
  BrowserRouter,
  Route,
  Switch
} from 'react-router-dom';
import Navbar from './components/Navbar'
import TyreSelect from './components/TyreSelect'
import FitterSelect from './components/FitterSelect'
import SlotSelect from './components/SlotSelect'
import Basket from './components/Basket'


const Routes = () => {
  return (
    <BrowserRouter>
      <Navbar />
      
      <Switch>
        <Route path='/tyreselect' component={TyreSelect} />
      </Switch>
      <Switch>
        <Route path='/fitterselect' component={FitterSelect} />
      </Switch>
      <Switch>
        <Route path='/slotselect' component={SlotSelect} />
      </Switch>
      <Basket />
    </BrowserRouter>
    
  )
}

export default Routes