import React, { useContext } from 'react'
import { OrderContext } from '../../services/OrderProvider/OrderProvider'

const Basket = () => {
  const orderContext = useContext(OrderContext);
  const order = orderContext.order;
  const toDlList = (obj) => {
    return (
      <dl>
        {
          Object.keys(obj).map(key => {
            let value = typeof obj[key] === "object" && obj[key] ? toDlList(obj[key]) : obj[key];
            return (
              <>
                <dt>{key}</dt>
                <dd>{value}</dd>
              </>
            )
          })
        }
      </dl>
    )

  }
  return (
    toDlList(order)
  )
}


export default Basket
